
import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/col'; 
import ProgressBar from 'react-bootstrap/ProgressBar';



const About = () => {
    const html = 80;
    const responsive = 95;
    const photoshop = 60;
   
  return (
    <section  style={{ color:"black",  marginLeft:"680px",marginTop:"50px"}} id='about' className='block about-block'>
      <Container fluid>
        <div style={{ marginTop:"-50px"}}>
       <h2>About us</h2>
       <div className='subtitle'>learn more about us
</div>


        </div>
      <Row>
        <Col    style={{ width:"650px", marginLeft:"-600px", marginTop:"30px"}} sm={6}>
        <img src='vinod.jpg' alt='image'
        style={{ marginLeft:"0px",width: "1200px", height:"380px" }}
        />
          </Col>
         <Col  style={{width:"650px", marginLeft:"0px", marginTop:"30px", paddingRight:"-200px"}} sm={6}> Welcome to Corporate, where professionalism meets innovation.
          We are dedicated to helping businesses and individuals achieve their goals with tailored solutions that inspire growth and success.
           Our team combines expertise, creativity, and technology to deliver services that truly make a difference. At Corporate, we value trust, integrity, and long-term partnerships. We believe every client deserves personalized attention and results that exceed expectations. Whether youre looking to strengthen your brand, streamline operations, or plan for the future, we are here to guide you every step of the way. 
         Corporate is more than a nameits your partner in progress.
         <div className='progress-block'>
         <h4>HTML / CSS / Javascript</h4>
         <ProgressBar now={html} label={`${html}%`} />
         </div>
         <div className='progress-block'>
         <h4>responsive</h4>
         <ProgressBar now={responsive} label={`${responsive}%`} />
         </div>
         <div className='progress-block'>
         <h4>Photoshop</h4>
         <ProgressBar now={photoshop} label={`${photoshop}%`} />
         </div>
         </Col>
      </Row>
    </Container>  

    </section>
  )
}

export default About