
import React from 'react'
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import './App.css'

const Cantact = () => {
  return (
    <section id='contact' className='block contact-block'>
        <Container fluid>
        <div className='title-holder'>
            <h2>Contact us</h2>
            <div className='subtitle'>get connected with us</div>
        </div>
         <Form className='contact-form'>
      <Row>
        <Col sm={4}>
          <Form.Control type='text' placeholder="enter your full name" required />
        </Col>
        <Col>
          <Form.Control  type='email' placeholder="enter your email" />
        </Col>
        <Col>
          <Form.Control  type='tel' placeholder="enter your contact number" />
        </Col>
      </Row>
      <Row>
        <Col sm={12}>
        <Form.Control as="textarea" placeholder="enter your message" />
        </Col>
      </Row>
      <div className='btn-holder'>
        <Button type="submit">submit</Button>
      </div>
    </Form>
        </Container>
        <div className='google-map'>
            <iframe  title="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d30740.70882199629!2d77.47365530281358!3d15.613614140091219!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bb6433596c8b619%3A0xfc8ab62c56912fa7!2z4LCs4LCo4LC14LCo4LGB4LCw4LGNLCDgsIbgsILgsKfgsY3gsLAg4LCq4LGN4LCw4LCm4LGH4LC24LGNIDUxODM1MA!5e0!3m2!1ste!2sin!4v1760874362276!5m2!1ste!2sin"  allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
        </div>
        <Container fluid>
           
        <div className='contact-info'>
            <ul>
               <li>
                <li className="fas fa-envelope" style={{ marginLeft:"110px"}}> </li>
               hello@domain.com
               </li>
               <li>
                <li className="fas fa-phone" style={{ marginLeft:"110px"}}></li>
                9032657813
               </li>
               <li>
                <li className="fas fa-map-marker-alt" style={{ marginLeft:"110px"}}></li>
                banavanur, india
               </li>
            </ul>
        </div>
   </Container>
    </section>
  )
}

export default Cantact